type Props = { title: string; products:any[];};
import {createClient} from '@/lib/supabase/server'
import { Key } from 'react';



export default async function CategorySection({ title,products }: Props) {

 
  return (
    <div className="category-section" id={title.replace(/\s+/g, '-').toLowerCase()}>
      <div className="category-header">
        <div className="category-title">{title}</div>
        <div className="category-actions">View All <i className="fas fa-arrow-right" /></div>
      </div>
      {
        products.length>0 && <div className="category-products">
        {products.map((item: { product_id: Key | null | undefined; image: string | Blob | undefined; name: string | undefined; }) => (
          <img key={item.product_id} src={item.image} alt={item.name} />
        ))}
      </div>
      }
      {/* <div className="category-products">
        {imageIds.map(id => (
          <img key={id} src={`/images/item${id}.jpg`} alt={`Product ${id}`} />
        ))}
      </div> */}
    </div>
  );
}