type Props = { products: any[]; };
export default function HotDeals({ products }: Props) {
  const responsiveTruncate = (str: string, maxLength: number, ellipsis = '...') =>{
    if (str.length <= maxLength) return str;
    return str.substr(0, Math.max(0, maxLength - ellipsis.length)) + ellipsis;
  }
  console.log(444,products)
  return (
    <section className="hot-deals-section">
      <div className="hot-deals-header">
        <h2 className="hot-deals-title">This Month's Hot Deals</h2>
        <p className="hot-deals-subtitle">Unmissable offers on selected premium statues.</p>
      </div>
      <div className="hot-deals-grid">
        {
          products.length > 0 && products.map((v, index) => (
            <div key={index} className="hot-deal-item">
            <img src={v.image} alt={`Hot Deal ${index}`} />
            <h4>{v.name}</h4>
            <p>{responsiveTruncate(v.description,40)}</p>
            <div className="price">${v.price}</div>
          </div>
          )
           
          )

        }
     
      </div>
    </section>
  );
}